// import addNewToLS from './addNewToLS' - addNewToLS(...arg)
import getLS from './getLocalStorage';
import {date, formatDate} from './const';
import alertPopUp from './alertPopUp';
import cleanOutDiv from './cleanOutDiv';

export default function addNewToLS(
    ksaNum,
    _vendor_0 = '',
    _name_1 = '',
    _address_2 = '',
    _nextTO_3 = 0,
    _nextToDateText_4 = 0,
    _nextToDate_5 = "0001-01-01T00:00:00.000Z",
    _lastToDate_6 = "0001-01-01",
    _CBK_7 = ''
) {
    
    let newKeyValue = {
            vendor_0: _vendor_0,
            name_1: _name_1,
            address_2: _address_2,
            nextTO_3: _nextTO_3,
            nextToDateText_4: _nextToDateText_4,
            nextToDate_5: _nextToDate_5,
            lastToDate_6: _lastToDate_6,
            CBK_7:_CBK_7  
    }

    if (localStorage.getItem("ksa")) { 
        let getJsonLS = JSON.parse(localStorage.getItem("ksa"));

        for (let ksaN in getJsonLS) {
            if (ksaN == ksaNum) {
                alertPopUp(`КСА ${ksaNum} уже существует.`);
            } else {
            let ls = { [ksaNum] : newKeyValue }
            getJsonLS = Object.assign(getJsonLS, ls)
            localStorage['ksa'] = JSON.stringify(getJsonLS);
            }
        }
        Object.assign(getJsonLS, newKeyValue);
    } else  {
        localStorage.setItem('ksa', '')
        let ls = { [ksaNum] : newKeyValue }
        localStorage['ksa'] = JSON.stringify(ls);
    }

    cleanOutDiv()
    getLS(formatDate, date)
}


