// alertPopUp('Error', & 'color');
// import & alertPopUp('text error, color non or info');


export default function alertPopUp(alertTxt, color) {
    let alertDiv = document.querySelector('#alert');
    let alertClose = document.querySelector('#alert-close');
    let textAlert = document.querySelector('#alert-text');

    alertDiv.classList.remove('uk-transition-slide-right');
    let addCloseMove = () => { document.querySelector('#alert-close .foreground').classList.add('close-loader-move') };
    let removeCloseMove = () => { document.querySelector('#alert-close .foreground').classList.remove('close-loader-move') };
    let hideAlert = () => { document.querySelector('#alert').classList.add('uk-transition-slide-right') };
    
    // Текст алерта из аргумента alertPopUp('') 
    textAlert.textContent = alertTxt;
    // Цвет блока если нет аргумента красный, с любым 'arg' - синий
    if (!color) {
        alertDiv.classList.add('uk-button-danger')
    } else {
        alertDiv.classList.add('uk-button-primary')
    }

    // анимация лоадера закрытия
    if (!alertClose.classList.contains('uk-transition-slide-right')) {
        addCloseMove();
    }

    // закрыть алерт через 6 сек
    setTimeout(() => { 
        hideAlert(),
        removeCloseMove();
    }, 6000);
    // закрыть алерт по клику
    alertClose.onclick = () => {
        hideAlert(),
        removeCloseMove();
    }
    
}