//
export default function navbar() {
    let openCanvas = document.querySelector('.uk-navbar-toggle');
    let closeCanvas = document.querySelector('#canvas-close');

    openCanvas.onclick = () => {
        setTimeout(() => { 
            closeCanvas.classList.remove("uk-transition-slide-right");
        }, 400);
    }

    closeCanvas.onclick = () => { closeCanvas.classList.add("uk-transition-slide-right") };

}